@use '@styles/abstracts/_variables.scss' as var;
@use '@styles/base/_mixins' as mixin;

.state-symbols {
  &-page {
    padding-bottom: 128.8px;

    @include mixin.small-laptop {
      padding-bottom: 100px;
    }
    
    @include mixin.tablet {
      padding-bottom: 80px;
    }

    @include mixin.small-tablet {
      padding-bottom: 64px;
    }
  }
}

.symbols {
  margin-bottom: 96px;
  color: var.$secondary;
  font-family: var.$font-family;
  font-size: 1.8rem;
  font-style: normal;
  font-weight: 300;
  line-height: 1.66;
  letter-spacing: 0.346px;

  @include mixin.small-tablet {
    margin-bottom: 64px;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &__title {
    margin-bottom: 48px;

    @include mixin.wide-tablet {
      margin-bottom: 32px;
    }

    @include mixin.small-tablet {
      margin-bottom: 16px;
    }
  }

  &__content {
    padding-right: 100px;

    @include mixin.wide-tablet {
      padding-right: 50px;
    }

    @include mixin.small-tablet {
      padding-right: 0;
    }
  }

  &__info {
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    gap: 24px;
    margin-top: 24px;

    @include mixin.small-tablet {
      margin-top: 32px;
    }
  }

  &__info-title {
    color: var.$gray;
    font-family: var.$font-family2;
    font-size: 1.4rem;
    font-style: normal;
    font-weight: 400;
    line-height: 1.4;
    letter-spacing: 0.1px;
    min-width: 249px;
  }

  img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
  }

  .symbol-gerb {
    @include mixin.small-tablet {
      max-width: 164px;
    }
  }
}